*{
	margin:0;
	padding:0;
	box-sizing:border-box;
}
.main{
	
}

.main{
	width:100vw;
	height:100vh;
	border:1px solid black;
	display:flex;
	justify-content:center;
	align-items:center;
	flex-direction:column;
}
.header{
	width:100vw;
	height:10vh;
	display:flex;
	justify-content:right;
	align-items:center;
}
#balance{
	margin-right:4px;
}
.box1{
	width:100vw;
	height:30vh;
	border:1px solid black;
	display:flex;
	justify-content:center;
	align-items:center;
	font-size:40px;
}
#playButton{
	height:10vh;
	width:30vw;
}
.box2{
	width:100vw;
	height:50vh;
	display:grid;
	grid-template-rows:repeat(2,1fr);
	grid-template-columns:repeat(2,1fr);
}
.grid{
	display:flex;
	justify-content:center;
	align-items:center;
	font-size:30px;
}